.project-section button {
  margin-top: 80px;
  text-decoration: underline;
}

.contact-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 80px;
  gap: 80px;
  padding: 8rem 115px;
  /* height: 100vh; */
