.contact-section .one,
.contact-section .two {
  color: var(--whiteShades);
  font-family: 'Rubik', sans-serif;
}

.contact-section .one .title p,
.contact-section .two .title p {
  color: var(--green);
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 12px;
}

.contact-section .one .title h2,
.contact-section .two .title h2 {
