.overlay {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  height: 100vh;
  width: 100%;
  z-index: 99;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay .img-container {
  width: 752px;
  height: 485px;
  background: white;
  border-radius: 8px;
}

.overlay .img-container img {
  width: 100%;
  height: 100%;
  border-radius: 8px;
}

.overlay .closeBtn {
  position: absolute;
  right: 4rem;
  top: 3rem;
  cursor: pointer;
  background: rgb(42, 59, 53);
  padding: 5px;
  border-radius: 50%;
}

.overlay .closeBtn:hover {
  background: rgb(63, 88, 80);
}
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
/* NAVBAR */
@media (max-width: 1014px) {

  /* CSS */
  * {
    overflow-x: hidden;
  }

  .topNavbar>.nav-btn {
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 0px;
    /* padding: 0.7rem 10px; */
  }

  .topNavbar .nav-btn>label {
    display: inline-block;

    /* width: 50px; */
    /* height: 50px; */
    padding: 15px 12px 12px 12px;
  }

  .topNavbar .nav-btn>label:hover,
  .topNavbar #nav-check:checked~.nav-btn>label {
