  .portfolioDetail .description-portfolio p {
    margin-top: 12px !important;
    margin-bottom: 60px;
    text-align: justify;
  }

  .portfolioDetail .description-portfolio button {
    margin-left: 5%;
    margin-bottom: 12px;
  }

  .portfolioDetail .description-portfolio button i {
    margin-right: 12px;
  }

  .overlay {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.8);
    height: 100vh;
    width: 100%;
    z-index: 99;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .overlay .img-container {
    width: 70%;
    max-width: 752px;
    height: 70%;
    max-height: 485px;
    overflow: hidden;
    background: white;
    border-radius: 8px;
  }

  .overlay .img-container img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
  }

  .overlay .closeBtn {
    position: absolute;
    right: 5%;
    top: 5%;
    cursor: pointer;
    background: rgb(42, 59, 53);
    padding: 5px;
    border-radius: 50%;
  }

  .overlay .closeBtn:hover {
    background: rgb(63, 88, 80);
  }
}

